	module.exports = {"data":{"post":{"html":"<p><a href=\"http://www.flickr.com/photos/51035717986@N01/3611248234\" title=\"View 'IMG_0164' on Flickr.com\"><img src=\"https://farm4.static.flickr.com/3313/3611248234_6ca0540c2e_s.jpg\" alt=\"IMG_0164\" border=\"0\" width=\"75\" height=\"75\" align=\"right\" /></a>All the news about elections (European and upcoming German) reminded Christopher of two things. One, because of his dual citizenship, he can vote twice as often as either of his parents. Secondly, if he were to run for German Chancellor*, he would campaign for the following.</p>\n<ol>\n<li>More schools</li>\n<li>More help for victims of natural disaster, and research to prevent such disasters</li>\n<li>Energy conservation, less oil and coal, more wind and solar</li>\n<li>Abolish war</li>\n</ol>\n<p>Sounds like a solid platform, but the devil is going to be in the details.</p>\n<p>*Yes, we realize he previously <a href=\"http://www.papascott.de/archives/2008/11/05/yes-we-can-in-2036/\">announced his candidacy for the US Presidency</a>. We'll issue a statement when we know which is correct.</p>","fields":{"title":"Christopher's Platform","date":"09 Jun 2009","url":"https://papascott.de/2009-06-09-christophers-platform/"}}},"pathContext":{"slug":"/2009-06-09-christophers-platform/","prev":{"fields":{"title":"Pesky Parliament","date":"08 Jun 2009","slug":"/2009-06-08-pesky-parliament/"}},"next":{"fields":{"title":"Anatomical Drawing","date":"13 Jun 2009","slug":"/2009-06-13-anatomical-drawing/"}}}}

